
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 50px;
}
.nav-list {
  opacity: 1;
  font-size: 13px;
  letter-spacing: 0.07px;
  display: flex;
  align-items: center;
  transition: opacity 0.3 linear;
}
.nav-list .nav__link {
  margin-right: 40px;
  color: #30333a;
  position: relative;
}
.nav-list .nav__link:hover:after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: -10px;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: #2020df;
}
.nav-list .btn {
  background-color: #2020df;
  border-radius: 5px;
  width: 113px;
  height: 46px;
  border: none;
  outline: none;
  transition: transform 0.2s linear;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-list .btn a {
  font-size: 13px;
  color: #fff;
}
.nav-list .btn:active {
  transform: translateY(1px);
}
.nav-list .btn:hover {
  cursor: pointer;
  transform: translateY(-1px);
}
.burger__btn {
  content: '';
  display: none;
  justify-content: space-between;
  flex-direction: column;
  padding: 0;
  border: none;
  color: inherit;
  background-color: transparent;
}
.burger__btn:focus {
  border: none;
  outline: none;
}
.burger__btn span {
  content: '';
  width: 30px;
  height: 5px;
  margin: 3px;
  border-radius: 2px;
  background-color: #2020df;
  display: inline-block;
}

body {
  font-family: 'Poppins', sans-serif;
  font-size: 17px;
  margin: 0;
  padding: 0;
}
h2,
h3,
h4,
h5 {
  padding: 0;
  margin: 0;
}
.wrapper {
  max-width: 908px;
  margin: 0 auto;
}
a {
  text-decoration: none;
}

.partnership {
  display: flex;
}
.partnership-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.partnership-photo-wrap {
  margin-left: -268px;
  display: block;
  content: '';
  width: 776px;
  order: -1;
}
.partnership-photo {
  display: flex;
  justify-content: center;
  align-items: center;
}
.partnership-photo img {
  width: 100%;
  height: auto;
}
.partnership-photo__title {
  font-size: 135px;
  color: #ffffff;
  font-weight: 700;
  font-family: Poppins;
  z-index: 3;
  position: absolute;
}
.partnership__box {
  display: block;
  width: 30%;
}
.partnership__title {
  font-size: 42px;
  font-weight: 600;
  font-family: inherit;
  color: #303239;
}
.partnership__txt {
  font-family: Poppins;
  font-size: 14px;
  line-height: 31.38px;
  font-weight: 300;
  color: #8c8c98;
  height: 190px;
  overflow: hidden;
}
.partnership-more {
  color: #df7c7c;
  font-size: 13px;
  font-family: Poppins;
  cursor: pointer;
}
.partnership-more i {
  margin-left: 5px;
  transition: margin-left 0.2s linear;
}
.partnership-more:hover i {
  margin-left: 10px;
}
.show-more__active {
  height: auto;
}

.presentation {
  height: 455px;
  position: relative;
}
.presentation .chat__pic {
  position: absolute;
  top: 65px;
  left: 380px;
}
.presentation .chat-icon__pic {
  position: absolute;
  top: 68px;
  right: -90px;
}
.presentation__title {
  font-size: 58px;
  color: #303239;
  font-family: inherit;
  font-weight: 600;
  margin: 0;
  padding: 162px 0 35px;
}
.presentation__subtitle {
  font-size: 19px;
  font-weight: 300;
  color: #30333a;
  padding: 0;
  margin: 0;
}
.presentation-logo__box {
  background-color: #a7a5ef;
  width: 464px;
  height: 129px;
  position: absolute;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom-left-radius: 35px;
  border-bottom-right-radius: 35px;
}

.process {
  padding: 150px 0;
}
.process__title {
  color: #303239;
  font-size: 58px;
  font-weight: 600;
  font-family: inherit;
  margin-bottom: 24px;
}
.process__subtitle {
  font-family: inherit;
  color: #303239;
  font-size: 19px;
  font-weight: 300;
  margin-bottom: 95px;
}
.process__wrap {
  display: flex;
  justify-content: space-between;
}
.process__item {
  width: 25.6%;
}
.process__item:hover .process-icon__box {
  background: rgba(32, 32, 223, 0.3);
}
.process-icon__box {
  width: 68px;
  height: 99px;
  background: #ecf2ff;
  border-radius: 30px 3px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.process__heading {
  margin: 45px 0 33px;
  font-family: inherit;
  font-size: 42px;
  color: #30323a;
  font-weight: 500;
}
.process__txt {
  color: #8c8c98;
  font-size: 17px;
  font-weight: 400;
  line-height: 28.39px;
}

.start {
  margin: 0 auto;
  max-width: 1070px;
  height: 238px;
  background-color: #5271ff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 75px 75px 75px 110px;
  box-sizing: border-box;
  position: relative;
  top: 100px;
}
.start__title {
  color: #ffffff;
  font-family: Poppins;
  font-size: 42px;
  font-weight: 500;
}
.start-contact {
  display: flex;
  align-items: center;
}
.start__mail {
  color: #ffffff;
  font-family: Poppins;
  font-size: 19px;
  font-weight: 400;
  margin-right: 77px;
}
.start__mail a {
  color: #fff;
}
.start__mail a:hover {
  cursor: pointer;
}
.start__btn {
  width: 175px;
  height: 69px;
  box-shadow: 0 18px 60px 10px rgba(0, 13, 196, 0.42);
  border-radius: 3px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.start__btn:hover {
  box-shadow: 0 18px 50px 10px rgba(0, 13, 196, 0.57);
}
.start__btn a {
  color: #30323a;
  font-family: Poppins;
  font-size: 17px;
  font-weight: 600;
  text-decoration: none;
}

.team {
  background: #ecf2ff;
  max-width: 1368px;
  margin: 0 auto;
}
.team__wrap {
  max-width: 1000px;
  height: 602px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.team-photo__content {
  height: 100%;
  position: relative;
  top: -4px;
}
.team-text__wrap {
  margin-top: 170px;
  width: 35.7%;
  position: relative;
  left: -60px;
  order: 1;
}
.team__title {
  font-family: inherit;
  color: #30323a;
  font-size: 40px;
  font-weight: 500;
  line-height: 1.1;
  margin-bottom: 34px;
}
.team__txt {
  font-family: inherit;
  color: #8c8c98;
  font-size: 17px;
  font-weight: 400;
  line-height: 26.72px;
  margin-bottom: 46px;
}
.team__names {
  color: #30323a;
  font-family: inherit;
  font-size: 19px;
  font-weight: 400;
}

.tools {
  margin-top: 140px;
  padding-bottom: 35px;
}
.tools__wrap {
  display: flex;
  justify-content: space-between;
}
.tools-text {
  width: 37%;
  margin-right: 30px;
  padding-top: 35px;
}
.tools-content {
  width: 476px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.tools-content img {
  margin-bottom: 28px;
  flex: none;
}
.tools__title {
  color: #30323a;
  font-family: inherit;
  font-size: 42px;
  font-weight: 600;
}
.tools__txt {
  color: #8c8c98;
  line-height: 31.38px;
}

.about {
  padding: 150px 0;
  display: flex;
  justify-content: space-between;
  flex: none;
}
.about-left {
  width: 46%;
}
.about__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
}
.about__item:hover .about-mark {
  background: #2020df;
}
.about__item:hover .about-mark .mark {
  color: #fff;
}
.about-mark {
  background: #ecf2ff;
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: none;
  border-radius: 15px 3px;
  margin-right: 42px;
}
.mark {
  color: #2020df;
  font-size: 14px;
}
.about__title {
  padding: 0;
  margin: 0;
  font-size: 32px;
  font-weight: 500;
  color: #30333a;
  line-height: 1;
}
.about__txt {
  font-size: 17px;
  color: #8c8c98;
  font-family: Ubuntu;
}

.footer {
  max-width: 1368px;
  background-color: #ecf2ff;
  margin: 0 auto;
  padding: 214px 0 55px;
}
.footer__wrap {
  display: flex;
  flex: none;
  justify-content: space-between;
  max-width: 908px;
  margin: 0 auto;
}
.footer-nav__container {
  width: 60%;
  display: flex;
  justify-content: space-between;
}
.footer-nav__item {
  font-size: 17px;
  font-family: Ubuntu;
  color: #8c8c98;
  font-weight: 400;
}
.footer-nav__item h5 {
  margin-bottom: 34px;
  color: #30323a;
  font-weight: 500;
  font-family: Poppins;
  font-size: 18px;
}
.footer-nav__item ul {
  padding: 0;
  margin: 0;
  font-weight: inherit;
  font-family: inherit;
}
.footer-nav__item li {
  display: block;
  font-size: 14px;
  padding: 0;
  margin: 0;
  font-family: inherit;
  list-style: none;
  margin-bottom: 18px;
}
.footer-nav__item li a {
  color: inherit;
  text-decoration: none;
}
.footer-nav__item li a:hover {
  text-decoration: underline;
}
.footer-logo__wrap {
  width: 16%;
}
.footer-logo__wrap p {
  color: #8c8c98;
  font-family: Ubuntu;
  font-size: 17px;
  font-weight: 400;
  line-height: 27.2px;
}
.footer-autor {
  margin-top: 80px;
  color: #8c8c98;
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  line-height: 23.38px;
}
.footer-autor .my__name {
  font-weight: 500;
  color: #171717;
}

